import { pages } from "projectConfig"
import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsStaticPage"

export default function HelpPage(props) {
  const QuestionBlock = styled.div`
    margin-bottom: 32px;
  `
  return (
    <>
      <GatsbySeo
        title={pages.faq.title}
        description={pages.faq.description}
        canonical={props.location.href}
        openGraph={{
          url: props.location.href,
          title: pages.faq.title,
          description: pages.faq.description,
        }}
      />
      <Layout>
        <section>
          <article className={"adaptive text"}>
            <h1>Ответы на частые вопросы</h1>
            <QuestionBlock>
              <h2>Как покупать?</h2>
              <ol>
                <li>
                  Ознакомиться с <Link to={"/rules"}>правилами магазина</Link>.
                </li>
                <li>Выбрать нужный товар.</li>
                <li>
                  Прочитать описание к выбранному товару, нажав на него и
                  перейдя на новую страницу.
                </li>
                <li>
                  Нажать кнопку "Купить". Ввести свою почту, к которой у вас
                  есть доступ.
                </li>
                <li>
                  После успешной оплаты подождать от 5-ти до 10-ти минут. За это
                  время ваш товар будет отправлен на вашу почту.
                </li>
              </ol>
            </QuestionBlock>
            <QuestionBlock>
              <h2>Куда обратиться по вопросам гарантии?</h2>
              <p>
                В случае возникновения любых проблем вы можете обратиться в нашу{" "}
                <Link to={"/contacts"}>техническую поддержку</Link>.
              </p>
            </QuestionBlock>
            <QuestionBlock>
              <h2>Какие товары не подлежат замене и возврату?</h2>
              <p>
                На странице каждого товара есть блок "гарантия" с подробным
                описанием условий. Все наши товары подлежат только замене.
                Возврат будет проиведен в случае невозможности замены товара.
              </p>
            </QuestionBlock>
            <QuestionBlock>
              <h2>В каких случаях гарантия не предоставляется?</h2>
              <p>
                В случае нарушения <Link to={"/rules"}>правил сайта</Link>.
              </p>
            </QuestionBlock>
            <QuestionBlock>
              <h2>Что делать, если гарантийный срок истек?</h2>
              <p>
                На большую часть наших товаров предоставляется гарантийный срок
                в размере 7-ми дней. Если срок возврата товара истек вы можете
                обратиться в техническую поддержку и получить скидку постоянного
                клиента в размере 5%.
              </p>
            </QuestionBlock>
            <QuestionBlock>
              <h2>Как заменить товар?</h2>
              <p>
                Обратиться в нашу{" "}
                <Link to={"/contacts"}>техническую поддержку</Link>. В обращении
                указать: почту и дату покупки.
              </p>
            </QuestionBlock>
          </article>
        </section>
      </Layout>
    </>
  )
}
